export default {
	data() {
		return {
			type:'password',
			isuser:true,        //用户名是否可用
			isemail:true,      //邮箱是否可用
			look:false,         //是否显示密码
			invalid:false,      //邮箱是否符合
			useemploy:false  ,  //用户名是否被占用
			emailemploy:false  ,//邮箱是否被占用
			lookurl:'../../static/images/sign/biyan.png',
			email:'',
			user:'',
			pws:'',
			isok:false
		}
	},
	methods: {
		isOk:function(){
			if(this.isuser&&this.isemail&&this.pws.length>5){
				this.isok=true
			}else{
				this.isok=false
			}
		},
		//密码显示隐藏方法
		looks:function(){
			if(this.look){
				this.type = "password";
				this.look=!this.look
				this.lookurl = '../../static/images/sign/biyan.png'
			}else{
				this.type="text";
				this.look=!this.look
				this.lookurl = '../../static/images/sign/look.png'
			}
		},
		//判断是否为邮箱格式
		isEmail:function(e){
			let reg = /^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/;
			this.email =e.detail.value
			if(this.email.length>0){
				if(reg.test(this.email)){
					this.invalid= false;
				}else{
					this.invalid= true;
				}
			}
			this.isOk()
		},
		//获取用户名
		getUser:function(e){
			this.user = e.detail.value;
			this.isOk()
		},
		//获取密码
		getPws:function(e){
			this.pws = e.detail.value;
			this.isOk()
		},
		//返回方法
		backOne:function(){
			uni.navigateBack({
			    delta: 1
			});
		},
		
		
		
	}
}
